import payMethods from '@/components/pay-methods'
import clickButton from '@/components/click-button'
import { recharge } from '@/api'
export default {
	data() {
		return {
			money: '',
			pay_way: 1
		};
	},
	methods: {
		async buttonClick () {
			if(this.money == "" || this.money.trim() == "") {
				uni.showToast({
					title: '请输入充值金额',
					icon: 'none'
				})
				return;
			}
			if(this.pay_way == 1) {   // 支付宝
				let result = await recharge ({
					token: uni.getStorageSync('token'),
					money: this.money,
					pay_way: this.pay_way
				})
				uni.requestPayment({
					provider: 'alipay',
					orderInfo: result, 
					success: function (res) {
						if(res.errMsg == "requestPayment:ok") {
							uni.redirectTo({
								url: '/pages/success/index?title=充值成功'
							})
						}
					},
					fail: function (err) {
						// uni.showToast({
						// 	title: err.errMsg,
						// 	icon: 'none'
						// })
						uni.redirectTo({
							url: '/pages/fail/index?title=充值失败'
						})
					}
				});
			}	
			if(this.pay_way == 2) {   // 微信
				let result = await recharge ({
					token: uni.getStorageSync('token'),
					money: this.money,
					pay_way: this.pay_way
				})
				console.log(JSON.stringify(result))
				uni.requestPayment({
					provider: 'wxpay',
					orderInfo: result, //微信
					success: function (res) {
						console.log('success:' + JSON.stringify(res));
						if(res.errMsg == 'requestPayment:ok') {
							uni.redirectTo({
								url: '/pages/success/index?title=充值成功'
							})
						}
					},
					fail: function (err) {
						console.log('err:' + JSON.stringify(err));
						// uni.showToast({
						// 	title: err.errMsg,
						// 	icon: 'none'
						// })
						uni.redirectTo({
							url: '/pages/fail/index?title=充值失败'
						})
					}
				});
				return;
			}	
		},
		payTypeClick (index) {
			if(index == 0) {
				this.pay_way = 1;
			}
			if(index == 1) {
				this.pay_way = 2;
			}
		}
	},
	components: {
		payMethods,
		clickButton
	}
}
